<template>
  <div id="app">
      <router-view></router-view>
      <Footer/>
  </div>

    
</template>

<script>
import Footer from './common/Footer'
export default {
  name: 'app',
  components: {
    Footer
  },
  data() {
    return {
      status:0
    };
  },
  methods: {
    getBaseInfo(){



    // this.$store.commit('setBaseUrlValue', localStorage.getItem('baseUrl'));
      this.$http({
        method: 'get', 
        url: 'base_info'
      }).then(res=>{

        this.$store.commit('setBaseNameValue', res.data.name);
        this.$store.commit('setBaseSeeNumValue', res.data.see_num);
        if(!localStorage.getItem('token')){
          this.$router.push({path:'/Home'})
        }
        this.$store.commit('setBaseInfoValue', res.data);
        

      })
    }

  },
  beforeCreate(){
    this.$store.commit('setBaseUrlValue', localStorage.getItem('baseUrl'));

  },
  created(){
    this.getBaseInfo();
  }
}
</script>

