<template>
  <van-tabbar v-if="show" v-model="active" active-color="#7e5678" :border="true" inactive-color="#979799">
    <van-tabbar-item
        v-for="(k,key) in item"
        replace
        :to="k.router"
        :key="key"
    >
      <span>{{k.title}}</span>
      <template #icon="props">
        <img :src="props.active ? k.icon.active : k.icon.noactive" :alt=k.title />
      </template>
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
export default {
  data() {
    return {
      show:false,
      active: 0,
      item:[
        {
          router:"/Home",
          title:"首页",
          icon:{
              active:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/首页选中.png",
              noactive:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/首页未选中.png"
          }
        },{
          router:"/Game",
          title:"预约",
          icon:{
            active:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/预约选中.png",
            noactive:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/预约未选中.png"
          }
        },{
          router:"/Video",
          title:"视频",
          icon:{
            active:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/视频选中.png",
            noactive:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/视频未选中.png"
          }
        },{
          router:"/Mine",
          title:"我的",
          icon:{
            active:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/我的选中.png",
            noactive:"http://mihuangame.oss-accelerate.aliyuncs.com/lottery/footer/我的未选中.png"
          }
        }
      ]
    };
  },
  methods: {

  },
  watch: {
    '$route'(to) {
      if (to.name == "home") {
        this.active = 0;
        this.show = true;
      } else if (to.name == "game") {
        this.active = 1;
        this.show = true;
      } else if (to.name == "video") {
        this.active = 2;
        this.show = true;
      } else if (to.name == "mine") {
        this.active = 3;
        this.show = true;
      }else {
        this.show = false;
      }
    }
  },
  created () {
    if (this.$route.name == "home") {
        this.active = 0;
        this.show = true;
    } else if (this.$route.name == "game") {
        this.active = 1;
        this.show = true;
    } else if (this.$route.name == "video") {
        this.active = 2;
        this.show = true;
    } else if (this.$route.name == "mine") {
        this.active = 3;
        this.show = true;
    }else {
        this.show = false;
    }
  },
};
</script>

<style lang='less' scoped>
@tabbar-height: 110px;
@tabbar-img:75px;
.van-tabbar {
  height: @tabbar-height;
}
.van-tabbar-item__icon img {
    height: 45px;
    margin-bottom: 4px;
}
.van-tabbar-item {
  font-size: 26px;
  color: red !;
}
.van-tabbar-item.van-tabbar-item--active{
  color: red !important;
}
</style>
